.email {
    width: 150px;
    height: 150px;
    margin-left: 176px;
}
h1 {
    padding-left: 80px;
    width: 300px;
    margin: 0 auto;
    margin-top: 100px;
    color: white;
}
.formContainer {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin:0 auto;
}
.about {
    height: 30px;
    font-size: 17px;
    outline: none;
    border-radius: 30px;
    padding: 5px 20px 5px 20px;
    border: 2px solid orange;
    transition: 1.5s;
    margin: 10px;
}
.about:hover {
    transition: 1.5s;
    border: 2px solid blue;
}
.message {
    border: 2px solid orange;
    font-size: 17px;
    padding: 5px 20px 5px 20px;
    height: 150px;
    max-height: 100%;
    resize: none;
    outline: none;
    transition: 2s;
    margin-top: 30px;
}
.message:hover {
    transition: 1.5s;
    border: 2px solid green;
}
button {
    width: 100px;
    height: 40px;
    margin-top: 30px;
    margin-left:33%;
    font-size: 17px;
    background-color: orange;
    border:1px dotted rgba(0, 0, 0, .6);
    outline: none;
}