.nav {
    display: flex;
    justify-content: space-between;
    height: 50px;
    font-size: 30px;
    border:2px solid #ccc;
    padding:0px 50px 20px 50px;
    background-color: rgba(0, 0, 0, .5);
}
.navItem {
    margin-top: 10px;
    text-decoration: none;
    color: #ccc;
    transition: 1s;
}
.navItem:hover {
    color:orange;
    transition: 1s;
}